.todo-task {
	display: -ms-flexbox;
	display: flex;
    height: 50px;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 15px;
}

.todo-task ~ .todo-task{
	margin-top: 0px;
}

.task-title {
	-ms-flex: 1 1 0%;
	    flex: 1 1 0%;
	padding: 0 15px;
}

.incomplete {
	color: white;
}

.complete {
	color: #403257;
}


